import { fabric } from 'fabric';
import React, { useRef, useEffect } from 'react';

interface IProps { }

const Canvas: React.FunctionComponent<IProps> = () => {
  const makeCircle = (uid: any, left: any, top: any, line1: any, line2: any, line3: any, line4: any, radius = 5) => {

    const c = new fabric.Circle({
      left: left,
      top: top,
      strokeWidth: 2,
      radius: radius,
      fill: 'white',
      stroke: 'black'
    });
  
    c.hasControls = c.hasBorders = false;
    //@ts-ignore
    c.line1 = line1;
    //@ts-ignore
    c.line2 = line2;
    //@ts-ignore
    c.line3 = line3;
    //@ts-ignore
    c.line4 = line4;
    //@ts-ignore
    c.uid = uid;
  
    return c;
  }
  
  const makeLine = (coords: any) => {
    return new fabric.Line(coords, {
      fill: 'white',
      stroke: 'black',
      strokeWidth: 2,
      selectable: false,
      evented: false
    });
  }
  
  const moving = (target: any, body: any, canvas: any) => {
  
    if (target.line1) {
      target.line1 && target.line1.set({ 'x2': target.left, 'y2': target.top });
    }
    if (target.line2) {
      target.line2 && target.line2.set({ 'x1': target.left, 'y1': target.top });
    }
    if (target.line3) {
      target.line3 && target.line3.set({ 'x1': target.left, 'y1': target.top });
    }
    if (target.line4) {
      target.line4 && target.line4.set({ 'x1': target.left, 'y1': target.top });
    }
  
    canvas.bringToFront(body.headCircle);
    canvas.renderAll();
  }

  const _foo = useRef(new fabric.Canvas('c'));

  useEffect(() => {    
    fabric.Object.prototype.originX = 'center';
    fabric.Object.prototype.originY = 'center';
    
    _foo.current = new fabric.Canvas('main-canvas', { selection: false, backgroundColor: 'white' });

    const offsetX = 0;
    const offsetY = 0;

    const lines: { [key:string]: any } = {
      headLine: makeLine([250 + offsetX, 125 + offsetY, 250 + offsetX, 175 + offsetY]),
      hipsLine: makeLine([250 + offsetX, 175 + offsetY, 250 + offsetX, 225 + offsetY]),

      rightElbowLine: makeLine([250 + offsetX, 175 + offsetY, 285 + offsetX, 200 + offsetY]),
      rightHandLine: makeLine([285 + offsetX, 200 + offsetY, 320 + offsetX, 225 + offsetY]),

      leftElbowLine: makeLine([250 + offsetX, 175 + offsetY, 215 + offsetX, 200 + offsetY]),
      leftHand: makeLine([215 + offsetX, 200 + offsetY, 180 + offsetX, 225 + offsetY]),

      rightKneeLine: makeLine([250 + offsetX, 225 + offsetY, 270 + offsetX, 275 + offsetY]),
      rightAnkleLine: makeLine([270 + offsetX, 275 + offsetY, 280 + offsetX, 315 + offsetY]),

      leftKneeLine: makeLine([250 + offsetX, 225 + offsetY, 230 + offsetX, 275 + offsetY]),
      leftAnkleLine: makeLine([230 + offsetX, 275 + offsetY, 220 + offsetX, 315 + offsetY]),
    }

    const circles: { [key:string]: any } = {
      headCircle: makeCircle('headCircle', lines.headLine.get('x1'), lines.headLine.get('y1'), null, lines.headLine, null, null, 24),

      shoulderCircle: makeCircle('shoulderCircle', lines.headLine.get('x2'), lines.headLine.get('y2'), lines.headLine, lines.hipsLine, lines.rightElbowLine, lines.leftElbowLine),

      hipsCircle: makeCircle('hipsCircle', lines.hipsLine.get('x2'), lines.hipsLine.get('y2'), lines.hipsLine, lines.rightKneeLine, lines.leftKneeLine, null),

      rightElbowCircle: makeCircle('rightElbowCircle', lines.rightElbowLine.get('x2'), lines.rightElbowLine.get('y2'), lines.rightElbowLine, lines.rightHandLine, null, null),
      rightHandCircle: makeCircle('rightHandCircle', lines.rightHandLine.get('x2'), lines.rightHandLine.get('y2'), lines.rightHandLine, null, null, null),

      leftElbowCircle: makeCircle('leftElbowCircle', lines.leftElbowLine.get('x2'), lines.leftElbowLine.get('y2'), lines.leftElbowLine, lines.leftHand, null, null),
      leftHandCircle: makeCircle('leftHandCircle', lines.leftHand.get('x2'), lines.leftHand.get('y2'), lines.leftHand, null, null, null),

      rightKneeCircle: makeCircle('rightKneeCircle', lines.rightKneeLine.get('x2'), lines.rightKneeLine.get('y2'), lines.rightKneeLine, lines.rightAnkleLine, null, null),
      rightAnkleCircle: makeCircle('rightAnkleCircle', lines.rightAnkleLine.get('x2'), lines.rightAnkleLine.get('y2'), lines.rightAnkleLine, null, null, null),

      leftKneeCircle: makeCircle('leftKneeCircle', lines.leftKneeLine.get('x2'), lines.leftKneeLine.get('y2'), lines.leftKneeLine, lines.leftAnkleLine, null, null),
      leftAnkleCircle: makeCircle('leftAnkleCircle', lines.leftAnkleLine.get('x2'), lines.leftAnkleLine.get('y2'), lines.leftAnkleLine, lines.leftFoot, null, null),
    }

    const grid = 20;
    const unitScale = 5;
    const canvasWidth = 100 * unitScale;
    const canvasHeight = 100 * unitScale;

    for (let i = 0; i < (canvasWidth / grid); i++) {
      _foo.current.add(new fabric.Line([i * grid, 0, i * grid, canvasHeight], { type: 'line', strokeWidth: .5, stroke: '#ccc', selectable: false }));
      _foo.current.add(new fabric.Line([0, i * grid, canvasWidth, i * grid], { type: 'line', strokeWidth: .5, stroke: '#ccc', selectable: false }))
    }

    const body = { ...lines, ...circles };
    for (const key in body) {
      _foo.current.add(body[key]);
    }

    _foo.current.on('object:moving', (e: any) => moving(e.target, body, _foo.current));

  }, []);

  return <canvas id='main-canvas' width={500} height={500} />;
};

export default Canvas;
